import * as React from 'react';
import { Handle, Position } from 'reactflow';

import { Box } from '@mui/system'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';


export default function StmtNode({data}) {
  
  function renderButton({data}){
    if (data.subGraphName != null){
      return <Button style={{ width: 150, height: 5, fontSize:7}} onClick={()=> data.fun(data.subGraphName)}>Learn More</Button>
    }else{
      return <></>
    }
  }



  return (
    <>
    <Handle type="target" position={Position.Top} />
    <Card style={{ width: 150, height: 150 }}>
      <CardContent style={{overflow: 'clip'}}>
        <Typography sx={{ fontSize: 7 }} color="text.secondary" gutterBottom>
          Statement
        </Typography>
        <Typography style={{ fontSize:10 }}variant="h5" component="div">
          {data.stmt}
        </Typography>
      </CardContent>
      <CardActions>
        {renderButton({data})}
      </CardActions>
    </Card>
    <Handle type="source" position={Position.Bottom} />
    </>
  );
}