import * as React from 'react';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import tablist from './tablist.json';
import ignorelist from "./ignorelist.json"

const tasks = tablist.filter(x => ignorelist.indexOf(x) === -1); // Hardcoded list of tasks
export default function TaskList({onTaskChange}) {
  return (
    <Box className = 'tabs'>
        <List>
        {tasks.map(task => (
          <ListItem disablePadding key={task}>
            <ListItemButton onClick={() => onTaskChange(task)}>
              <ListItemText primary={task} align ='center'/> 
            </ListItemButton>
          </ListItem>
        ))}
        </List>
    </Box>
  );
}