import React, { useState, useEffect } from 'react';
import { Grid } from '@mui/material';
import Graph from './Graph';
import TaskList from './TaskList';
import 'reactflow/dist/style.css';
import './App.css'

function App() {
  const [data, setData] = useState({ nodes: [], edges: [] });
  const [TaskName, setTaskName] = useState('t0');

  const handleTaskChange = (fileName) => {
    fetch(`flow/${fileName}.json`)
      .then(response => { console.log(response); return response.json() })
      .then(jsonData => {
        setTaskName(fileName);
        const nodes = jsonData.nodes;
        const edges = jsonData.edges;
        setData({ nodes, edges });
      });
  };

  useEffect(() => {
    handleTaskChange('t0');
  }, []);

  function generateTaskString(inputString) {
    // Use regular expression to extract the number from the input string
    const randomNumber = parseInt(inputString.match(/\d+/)[0], 10);
  
    // Combine "task" with the random number and return the result
    const taskString = "Task " + randomNumber;
    return taskString;
  }

  return (
    <Grid container>
      <div className = 'title'>
        {generateTaskString(TaskName)}
      </div>
      <Grid item xs={3}>
        <TaskList onTaskChange={handleTaskChange} />
      </Grid>
      <Grid item xs={9}>
        <Graph data={data} handleTaskChange={handleTaskChange}/>
      </Grid>
    </Grid>

  );
};

export default App;
