import React, { memo } from 'react';
import { Handle, Position } from 'reactflow';
import Card from '@mui/material/Card';
import Typography from '@mui/material/Typography';
import CardContent from '@mui/material/CardContent';

export default memo(({ data, isConnectable }) => {

  return (
    <Card style={{ width: 150, height: 150 }}>
      <Handle
        type="target"
        position={Position.Top}
        id="TopIN"
        isConnectable={isConnectable}
      />
      <CardContent style={{overflow: 'clip'}}>
        <Typography sx={{ fontSize: 7 }} color="text.secondary" gutterBottom>
          Value
        </Typography>
        <Typography sx={{ fontSize: 10 }}> Var: {data.name} </Typography>
        <div className='TextBox'>
          <div className='Text'>{data.label}</div>
        </div>
      </CardContent>
      <Handle
        type="source"
        position={Position.Bottom}
        id="BottomOUT"
        isConnectable={isConnectable}
      />
    </Card>
    );
});